import axios from '../helpers/axios';
import store from '../store';
import { cartConstants } from './constants';

export const AddToCart = (product, newQty = 1) => {
    console.log(newQty);
    return async dispatch => {
        const { cart: { cartItems }, auth } = store.getState();
        const qty = cartItems[product._id] ? parseInt(cartItems[product._id].qty + newQty) : 1;
        cartItems[product._id] = {
            ...product,
            qty
        }
        if (auth.authenticate) {
            dispatch({ type: cartConstants.ADD_TO_CART_REQUEST });
            const payload = {
                cartItems: [
                    {
                        product: product._id,
                        quantity: qty
                    }
                ]
            };
            const res = await axios.post(`/user/cart/addtocart`, payload);
            if (res.status === 201) {
                dispatch(getCartItems());
            }
        }
        else {
            localStorage.setItem('cart', JSON.stringify(cartItems));
        }
        dispatch({
            type: cartConstants.ADD_TO_CART_SUCCESS,
            payload: { cartItems }
        })
    }
}

export const UpdateCart = () => {
    return async dispatch => {
        const { auth } = store.getState();
        let cartItems =null;
        if(store.getState('cartItems') !== null){
            cartItems = JSON.parse(localStorage.getItem('cart'))
        }
        if (auth.authenticate) {
            localStorage.removeItem('cart');
            if (cartItems) {
                const payload = {
                    cartItems: Object.keys(cartItems).map((key, index) => {
                        return {
                            quantity: cartItems[key].qty,
                            product: cartItems[key]._id
                        }
                    })
                };
                if (Object.keys(cartItems).length > 0) {
                    const res = axios.post(`/user/cart/addtocart`, payload);
                    if (res.status === 201) {
                        dispatch(getCartItems());
                    }
                }
            }
        }
        else {
            if (cartItems) {
                dispatch({
                    type: cartConstants.ADD_TO_CART_SUCCESS,
                    payload: { cartItems }
                })
            }
        }
    }

}
const getCartItems = () => {
    return async dispatch => {
        try {
            dispatch({ type: cartConstants.ADD_TO_CART_REQUEST });
            const res = await axios.post(`/user/cart/getCartItems`);
            if (res.status === 200) {
                const { cartItems } = res.data;
                console.log({ getCartItems: cartItems })
                if (cartItems) {
                    dispatch({
                        type: cartConstants.ADD_TO_CART_SUCCESS,
                        payload: { cartItems }
                    })
                }
            }
        }
        catch (error) {
            console.log(error);
        }
    }
}

export {
    getCartItems
}